<template>
  <div class="myHome">
    <div style="padding: 20px">
      <!--面包屑-->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '' }">
          <span style="color: white">资源管理</span>
        </el-breadcrumb-item>
        <el-breadcrumb-item>
          <span style="color: white">已下架资源</span>

        </el-breadcrumb-item>
      </el-breadcrumb>
      <div>
        <div style="padding: 10px ;float: left">
          <el-input style="width: 200px" placeholder="请输入资源包名" suffix-icon="el-icon-search" class="ml-5"
                    v-model="packageName"></el-input>
        </div>
        <div style="padding: 10px ;float: left">
          <el-input style="width: 200px" placeholder="请输入上传者名称" suffix-icon="el-icon-search"
                    v-model="packageAuthor"></el-input>
        </div>
        <div style="padding: 10px ;float: left">
          <el-button style="margin-left: 10px" type="primary" @click="load">搜索</el-button>
        </div>

        <div style="padding: 10px ;float: left;">
          <el-button style="margin-left: 10px" type="success" @click="clearFilter">重置筛选条件</el-button>
        </div>
      </div>
      <el-table :data="tableData"
                border stripe
                :header-cell-style="{background:'#42b983',color:'white'}"
                @selection-change="handleSelectionChange"
                :stripe=true
                :highlight-current-row="true"
                :row-class-name="tabRowClassName"
                style="font-size: 15px"
                max-height="600"
      >
        <el-table-column prop="beRemovedPackageName" label="资源包名" min-width="15%" :resizable="false">
        </el-table-column>
        <el-table-column prop="packageCnName" label="资源中文名" min-width="15%" :resizable="false">
        </el-table-column>
        <!--待开发，这里加一个向下展开资源包详情的功能-->
        <el-table-column prop="beRemovedPackageType" label="资源包类型" min-width="15%" :resizable="false">
        </el-table-column>
        <el-table-column prop="beRemovedPackageAuthor" label="资源包上传者" min-width="15%" :resizable="false">
        </el-table-column>
        <el-table-column prop="beRemovedPackageTime" label="下架时间" min-width="15%" :resizable="false">
        </el-table-column>

        <!--待开发，操作：重新上架、彻底删除-->
        <el-table-column prop="" label="操作" min-width="40%" align="center">
          <template v-slot="scope">
            <el-button type="success" size="mini"
                       @click="getPackageDetailInfoButton(scope.row.beRemovedPackageId,scope.row.beRemovedPackageName,scope.row.connectedDetailInfoId)">
              <i class="el-icon-view">查看详情</i>
            </el-button>
            <el-button type="danger"
                       @click="packageDeleteButton(scope.row.beRemovedPackageId,scope.row.beRemovedPackageName,scope.row.connectedDetailInfoId)">
              <i class="el-icon-error">删 除</i>
            </el-button>
            <el-button type="primary"
                       @click="publishPackageAgainstButton(scope.row.beRemovedPackageId,scope.row.beRemovedPackageName,scope.row.connectedDetailInfoId)">
              <i class="el-icon-s-promotion">重新上架</i>
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--各种操作的对话框-->
      <!--查看详情-->
      <el-dialog title="资源包基础信息" :visible.sync="packageDetailDialogFormVisible">
        <el-card>
          <div slot="header" style="text-align: left">
            <span>{{ this.packageNameInDialog }}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">资源包名:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.connectedPackageOriginalFileName}}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">资源包大小:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.connectedPackageSize}}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">服务中文名:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.softwareCnName}}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">运行环境:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.softwareEnvironment }}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">服务版本号:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.softwareVersion }}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">服务唯一标识:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.softwareUniqueName }}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">容器服务CPU需求(cpuRequests):</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.cpuRequests}}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">容器服务内存需求(memoryRequests):</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.memoryRequests }}</span>
          </div>

          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">服务开发者:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.developerName }}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">服务开发单位:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.developerGroup}}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">联系方式:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.developerPhone}}</span>
          </div>

        </el-card>

        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="packageDetailDialogFormVisible = false">确 定</el-button>
        </div>
      </el-dialog>




      <!--彻底删除-->
      <el-dialog title="删除此资源包" :visible.sync="deletePackageDialogFormVisible">
        <el-card>
          <div slot="header" style="text-align: left">
            <span>{{ this.packageNameInDialog }}</span>
            <span>{{ this.packageIdInDialog }}</span>
          </div>
        </el-card>
        <span><i class="el-icon-warning"></i>删除资源包，此操作不可逆。</span>
        <div slot="footer" class="dialog-footer">
          <el-button type="info" @click="deletePackageDialogFormVisible = false">取 消</el-button>
          <el-button type="danger" @click="packageDelete">确 定</el-button>
        </div>
      </el-dialog>
      <!--重新发布-->
      <el-dialog title="发布资源包" :visible.sync="publishPackageAgainstDialogFormVisible">
        <el-card>
          <div slot="header" style="text-align: left">
            <span>{{ this.packageNameInDialog }}</span>
          </div>
        </el-card>
        <div slot="footer" class="dialog-footer">
          <el-button type="info" @click="publishPackageAgainstDialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="publishPackageAgainst">确 定</el-button>
        </div>
      </el-dialog>


      <div style="padding: 10px 0;background-color: white">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pageNum"
          :page-sizes="[2, 5, 10,20]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>

      </div>
    </div>
  </div>


</template>

<script>
import '../css/myCss.css'
export default {
  name: "BeRemovedPackage",
  data() {
    return {
      // 表格
      tableData: [],
      pageNum: 1,
      pageSize: 10,
      total: 0,
      packageName: "",
      packageAuthor: "",
      // 批量选择
      multipleSelection: [],

      // 操作的时候，把当前用户名传回去，用作记录操作者日志
      user: window.sessionStorage.getItem('userAccount'),


      //  各个显示窗的状态
      // 当前对话框里的id,name，从scope里读出来，有用
      packageIdInDialog: '',
      packageNameInDialog: '',
      packageStatusInDialog: '',
      packageConnectedDetailId:'',

      // 详情
      // 对话框
      packageDetailDialogFormVisible: false,
      // 后端传回来的res.data
      packageDetailInfo:'',

      // 彻底删除
      deletePackageDialogFormVisible: false,

      // 重新上架
      publishPackageAgainstDialogFormVisible: false,

    }
  },
  created() {
    this.load();
  },
  methods: {
    async load() {
      await this.request.get("/removedPackage/page", {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          packageName: this.packageName,
          packageAuthor: this.packageAuthor
        }
      }).then(res => {
        console.log(res)
        //tableData是前端声明的数据，records是mybatisPlus分页查询自定义的JSON里的参数，因此这边records前端会说它不认识
        this.tableData = res.records
        this.total = res.total
      })
      this.getPendingReviewPackageAsideNumber();
      this.getPublishedPackageAsideNumber();
      this.getRemovedPackageAsideNumber();
    },
    // 读取侧边栏数据
    getPendingReviewPackageAsideNumber() {
      this.request.get("/pendingReviewPackage/getPendingPackageAsideNumber").then(res => {
        this.$store.commit('setPendingPackageNumber', res.data);
      })
    },
    getPublishedPackageAsideNumber() {
      this.request.get("/publishedPackage/getPublishedPackageAsideNumber").then(res => {
        this.$store.commit('setPublishedPackageNumber', res.data);
      })
    },
    getRemovedPackageAsideNumber() {
      this.request.get("/removedPackage/getRemovedPackageAsideNumber").then(res => {
        this.$store.commit('setRemovedPackageNumber', res.data);
      })
    },

    //清除搜索条件
    clearFilter() {
      this.packageName = "";
      this.packageAuthor = "";
      this.load();
    },
    // 查看详情
    getPackageDetailInfoButton(id, name,detailId) {
      this.packageIdInDialog = id;
      this.packageNameInDialog = name;
      this.packageConnectedDetailId = detailId;
      this.request.post("/servicePackageDetailInfo/getServicePackageDetailInfoById", {
        params: {
          packageConnectedDetailId: this.packageConnectedDetailId
        }
      }).then( res => {
        if (res.code === '200') {
          this.packageDetailDialogFormVisible = true;
          this.packageDetailInfo = res.data;
        }
      })
    },

    // 彻底删除
    packageDeleteButton(id, name,detailId) {
      this.packageIdInDialog = id;
      this.packageNameInDialog = name;
      this.deletePackageDialogFormVisible = true;
      this.packageConnectedDetailId = detailId;
    },
    async packageDelete() {
      await this.request.post("/removedPackage/deletePackageById", {
        params: {
          packageId: this.packageIdInDialog,
          operator: this.user,
          servicePackageDetailInfoId: this.packageConnectedDetailId
        }
      }).then(res => {
        if (res.code === '200') {
          this.$message.success(res.msg)
        } else {
          this.$message.warning("系统异常，操作失败")
        }
      })
      // 重新Load刷新页面获取数据
      await this.load();
      this.deletePackageDialogFormVisible = false;
    },

    //重新上架
    publishPackageAgainstButton(id, name,detailId) {
      this.packageIdInDialog = id;
      this.packageNameInDialog = name;
      this.publishPackageAgainstDialogFormVisible = true;
      this.servicePackageDetailInfoId = detailId;
    },

    async publishPackageAgainst() {
      await this.request.post("/removedPackage/publishPackageAgainstById", {
        params: {
          packageId: this.packageIdInDialog,
          operator: this.user,
          servicePackageDetailInfoId: this.packageConnectedDetailId
        }
      }).then(res => {
        if (res.code === '200') {
          this.$message.success(res.msg)
        } else {
          this.$message.warning("系统异常，操作失败")
        }
      })
      // 重新Load刷新页面获取数据
      await this.load();
      this.publishPackageAgainstDialogFormVisible = false;
    },
    handleSizeChange(pageSize) {
      console.log(pageSize)
      this.pageSize = pageSize
      this.load() //刷新数据
    },
    handleCurrentChange(pageNum) {
      console.log(pageNum)
      this.pageNum = pageNum
      this.load() //刷新数据
    },


  },
}
</script>

<style scoped>

</style>
